const calculateStartPage =(currentPage)=>{
    if (currentPage){
        if (currentPage<10){
            return 1
        }
        if (currentPage<100 && currentPage>10){
          let cur = (currentPage/100).toFixed(2)
            let res = cur.toString().split('').slice(0,-1).join('')
            
            if (cur.split('').slice(-1) == 0)
            {
                return Number(res)*100-10+1
            }

            return Number(res)*100+1
        }
        if (currentPage<1000 && currentPage>100){
            let cur = (currentPage/1000).toFixed(3)
            let res = cur.toString().split('').slice(0,-1).join('')
            if (cur.split('').slice(-1) == 0)
            {
                return Number(res)*1000-10+1
            }
              return Number(res)*1000+1
          }

          if (currentPage<10000 && currentPage>1000){
            let cur = (currentPage/10000).toFixed(4)
            let res = cur.toString().split('').slice(0,-1).join('')
            if (cur.split('').slice(-1) == 0)
            {
                return Number(res)*10000-10+1
            }
            
              return Number(res)*10000+1
          }
        
    }
    
    return 1
    
  }

export default calculateStartPage