

export const required = (value)=>{
 if (value) return undefined

 return 'error, please fill in the required field'
}

export const maxLenght = (length) =>{
    

    return (value)=>{
    
    if (value.length > length) return `error, max lenght ${length}`

    return undefined
    }

}

export const urlValidation = (value) =>{
    const myRegExp =/^(?:\S+(?::\S*)?@)?(?:(?!10(?:\.\d{1,3}){3})(?!127(?:\.\d{1,3}){3})(?!169\.254(?:\.\d{1,3}){2})(?!192\.168(?:\.\d{1,3}){2})(?!172\.(?:1[6-9]|2\d|3[0-1])(?:\.\d{1,3}){2})(?:[1-9]\d?|1\d\d|2[01]\d|22[0-3])(?:\.(?:1?\d{1,2}|2[0-4]\d|25[0-5])){2}(?:\.(?:[1-9]\d?|1\d\d|2[0-4]\d|25[0-4]))|(?:(?:[a-z\u00a1-\uffff0-9]+-?)*[a-z\u00a1-\uffff0-9]+)(?:\.(?:[a-z\u00a1-\uffff0-9]+-?)*[a-z\u00a1-\uffff0-9]+)*(?:\.(?:[a-z\u00a1-\uffff]{2,})))(?::\d{2,5})?(?:\/[^\s]*)?$/i;
   
    if (value && value.length>20){
   
    return 'error, max length 20'
    
    }
    
    else {

        if (value && !myRegExp.test(value) ) return "Not valid url"
        return undefined

    }
    

    

}


